'use client'

import Link from 'next/link';
import { usePathname } from "next/navigation";

export default function Navigation() {
    let paths = usePathname().split("/");
    paths.shift();
    let strSep = '/';
    let arr = [];
    let ctr = 0;
    // for (let i = 0; i < paths.length; i++) {
    //     arr.push(strSep + paths[i]);
    //     for (let j = i + 1; j < paths.length; j++) {
    //         arr.push(arr[i] + strSep + paths[j]);
    //     }
    // }


    arr.push(strSep + paths[ctr]);
    while (arr.length !== paths.length) {
        // ctr++;
        console.log('ctr', ctr, 'arr.length', arr.length);
        arr.push(arr[ctr++] + strSep + paths[ctr]);
    }

    console.log('paths', paths);
    console.log(usePathname());
    console.log('arr', arr);

    return (
        <div>You are here: {/*paths.map(p => <Link href={'/' + p}><u key={p}>{'/' + p}</u></Link>)*/}</div>
    );
}
